<template>
	<template-auth 
    titulo="Faça o seu cadastro" 
    :progressLoader="progressLoader"
    :alert="alertInfo"
  >
    <form @submit.prevent="cadastrar">
      <v-text-field
        id="name"
        label="Nome"
        v-model="name"
        v-on:keyup.enter="focusToNext('email')"
      />
      
      <v-text-field
        ref="email"
        v-model="email"
        label="E-mail"
        :rules="emailRules"
        v-on:keyup.enter="focusToNext('password')" 
      />
      
      <v-text-field
        ref="password"
        :type="showPassword ? 'text' : 'password'"
        v-model="password"
        :rules="passwordRules"
        label="Senha"
        :append-icon="showPassword ? 'visibility_off' : 'remove_red_eye'"
        @click:append="showPassword = !showPassword"
        v-on:keyup.enter="focusToNext('confirmPassword')" 
      />
      
      <v-text-field
        ref="confirmPassword"
        :type="showConfirmPassword ? 'text' : 'password'"
        v-model="confirmPassword"
        :rules="passwordRules"
        label="Confirmar Senha"
        :append-icon="showConfirmPassword ? 'visibility_off' : 'remove_red_eye'"
        @click:append="showConfirmPassword = !showConfirmPassword"
        v-on:keyup.enter="enterClick" 
      />

      <div class="form-group">
        <v-switch
          v-model="isBarber"
          label="barbeiro?"
          color="secondary"
        ></v-switch>
      </div>
      
      <div class="text-center mt-4">
        <btn-confirm
          text="Cadastrar"
          :disabled="btnDisabled"
          @clicked="cadastrar"
        />
      </div>

      <div class="text-center mt-4">
        <span class="d-block font-weight-light">ou</span>
        <btn-secondary
          class="mt-4"
          text="Fazer login"
          @clicked="goTo('login')"
        />
      </div>
    </form>
  </template-auth>
</template>

<script>
// Modules
import Axios from "@/plugins/axios"
// Components
import BtnConfirm from '@/components/globais/buttons/confirm/BtnConfirm'
import BtnSecondary from '@/components/globais/buttons/secondary/BtnSecondary'
import TemplateAuth from '@/pages/auth/template/Template'
// Mixins and MOdules
import FormMixin from '@/mixins/FormMixin'
import NavigationMixin from '@/mixins/NavigationMixin'
import SnackbarMixin from '@/mixins/SnackbarMixin'
import ValidationMixin from '@/mixins/ValidationMixin'

export default {
  name: 'Register',
  components: { BtnConfirm, BtnSecondary, TemplateAuth },
  mixins: [ FormMixin, NavigationMixin, SnackbarMixin, ValidationMixin ],
  data: () => ({
    alertInfo: null,
    showPassword: false,
    showConfirmPassword: false,
    name: '',
    email: '',
    isBarber: false,
    password: '',
    confirmPassword: '',
    progressLoader: false
  }),
  computed: {
    btnDisabled () {
      if (!this.isNameValid(this.name)) return true
      else if (!this.isEmailValid(this.email)) return true
      else if (!this.isPasswordValid(this.password)) return true
      else if (!this.isPasswordValid(this.confirmPassword)) return true
      else if (this.confirmPassword !== this.password) return true
      return false
    }
  },
  mounted () {
    document.querySelector('#name').focus()
  },
  methods: {
    enterClick () {
      if (!this.btnDisabled) this.cadastrar()
      else this.showSnackbarErro('Preencha todos os dados corretamente!')
    },
    cadastrar () {
      this.progressLoader = true
      let url = this.isBarber ? '/auth/barber/register' : '/auth/user/register'
      Axios.post(url, {
        name: this.name,
        email: this.email,
        password: this.password
      })
      .then(response => {
        this.progressLoader = false
        if (response.data.codigo === 200) this.alertInfo = "Obrigado por ter se cadastrado!<br>Verifique sua caixa de e-mail e confirme o seu cadastro."
        else this.showSnackbarErro(response.data.corpo)
      })
      .catch (() => { 
        this.progressLoader = false
        this.showSnackbarErro('Não foi possível fazer seu cadastro!') 
      })
		}
  }
}
</script>